ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333333;

  li {
    float: left;

    a {
      display: block;
      color: white;
      text-align: center;
      padding: 16px;
      text-decoration: none;

      &:hover {
        background-color: #111111;
      }

      &.active {
        background-color: #111111;
      }
    }
  }

}

